<template>
  <el-card shadow="always">
    <div class="app-side">
      <div class="side-title">
        我的追剧
        <div class="refresh-btn" @click="handleRefresh">
          <i class="el-icon-refresh"></i>
        </div>
      </div>
      <div class="follow-area" style="margin-top: 20px;height: calc(100vh - 160px);overflow-y: scroll;" v-if="followData && followData.length > 0">
        <div class="follow-wrap" v-for="(follow,index) in followData">
          <i class="el-icon-error del-btn" @click="handleDelFollow(follow,index)"></i>
          <a :href="follow.home_url" target="_blank">
            <div class="follow-item">
              <el-row :gutter="10">
                <el-col :span="5">
<!--                  <el-badge :value="parseInt(follow.episode)" :type="badgeType"><div class="movie-category">{{ follow.category }}</div></el-badge>-->
                  <div class="movie-category">{{ follow.category }}</div>
                </el-col>
                <el-col :span="19">
                  <div class="movie-title">{{ follow.title }}<i :id="'update' + index" class="el-icon-message-solid" style="display: none;margin-left: 5px;color: #f40e0e;"></i></div>
                  <div class="episode-item">更新至第 <span :id="'episode' + index" class="episode-num">{{ follow.episode }}</span> 集</div>
                </el-col>
              </el-row>
            </div>
          </a>
        </div>
      </div>
    </div>
  </el-card>
</template>

<script>
import Bus from '../bus'
import { btdx,meijutt,rijutv,hanjuba } from "../crawler"

export default {
  name: "AppSide",
  data(){
    return {
      followData: [],
      badgeType: 'warning',
    }
  },
  methods: {
    handleDelFollow(follow,index){
      this.followData.splice(index,1)
      this.handleRemoveFollowData(follow,index)
    },
    handleGetFollowData(){
      let follow_data = localStorage.getItem('follow_data')
      if(follow_data == null){
        follow_data = []
      } else {
        follow_data = JSON.parse(follow_data)
      }
      return follow_data
    },
    handleRemoveFollowData(data,index){
      let follow_data = localStorage.getItem('follow_data')
      if(follow_data !== null) {
        follow_data = JSON.parse(follow_data)
        if(follow_data.length > 0){
          let flag = follow_data.some((item) => {
            if(item['title'] === data['title'] && item['home_url'] === data['home_url']){
              return true
            }
          })
          if(flag){
            follow_data.splice(index,1)
            localStorage.setItem('follow_data',JSON.stringify(follow_data))
            this.$message.success(data['title'] + '已从追剧列表中删除~')
          }
        }
      }
    },
    async handleRefresh(){
      if(this.followData.length > 0){
        for (const key in this.followData) {
          let follow_data = JSON.parse(JSON.stringify(this.followData))
          let episode = 0
          if(follow_data[key]['site'] === 'hanjuba'){
            episode = await hanjuba.fetchEpisode(follow_data[key]['home_url'])
          } else if(follow_data[key]['site'] === 'rijutv'){
            episode = await rijutv.fetchEpisode(follow_data[key]['home_url'])
          } else if(follow_data[key]['site'] === 'meijutt'){
            episode = await meijutt.fetchEpisode(follow_data[key]['home_url'])
          }

          // 更新至element中
          if(episode > follow_data[key]['episode']){
            let episodeElement = document.getElementById('episode' + key)
            episodeElement.style.color = '#F56C6C'
            episodeElement.style.fontWeight = 'bold'
            let updateElement = document.getElementById('update' + key)
            updateElement.style.display = 'inline-block'
            this.followData[key]['episode'] = episode
            this.followData.unshift(this.followData.splice(key, 1)[0])
          }
        }
        localStorage.setItem('follow_data',JSON.stringify(this.followData))
        document.getElementsByClassName('follow-area')[0].scrollTop = 0 // 滚动条恢复至初始位置
      }
    },
  },
  mounted() {
    this.followData = this.handleGetFollowData()
    Bus.$on('addFollow', (data)=>{
      this.followData.push(data)
    })
  }
}
</script>

