import axios from 'axios'
const cheerio = require('cheerio')
const iconv = require('iconv-lite')

// axios.defaults.timeout = 10000

const meijutt = {
  async fetchList(url){
    try {
      return new Promise(((resolve,reject) => {
        axios.get(url, {
          responseType: 'arraybuffer',
          headers: {
            "Access-Control-Allow-Origin": "*",
          }
        }).then(res => {
          let str = iconv.decode(Buffer.from(res.data), 'gb2312')
          let html = iconv.encode(str, 'utf8').toString()
          let movies = []
          const cheerioObj = cheerio.load(html)
          let movieList = cheerioObj('.cn_box2')
          if(movieList){
            for (const itemElement of movieList) {
              let poster = cheerioObj('.cn_box_box3 > .bor_img3_right > a > img', itemElement).attr('src')
              let home_url = 'https://www.meijutt.tv' + cheerioObj('.cn_box_box3 > .bor_img3_right > a', itemElement).attr('href')
              let title = cheerioObj('.cn_box_box3 > .bor_img3_right > a', itemElement).attr('title')
              let introduction = ''
              let annotation_info = cheerioObj('.list_20', itemElement)
              let category = cheerioObj(cheerioObj('li', annotation_info)[6]).text()
              let episode = cheerioObj(cheerioObj('li', annotation_info)[1]).text()
              let last_date = ''
              let premiere_date = cheerioObj(cheerioObj('li', annotation_info)[5]).text()
              movies.push({
                'poster': poster,
                'home_url': home_url,
                'title': title,
                'name': title,
                'introduction': introduction,
                'last_date': last_date,
                'premiere_date': premiere_date,
                'category': category,
                'episode': episode
              })
            }
          }
          resolve(movies)
        }).catch(error => {
          reject(error)
        })
      }))
    }catch (error){
      console.log(error)
    }
  },
  async fetchEpisode(url){
    try {
      return new Promise(((resolve,reject) => {
        axios.get(url,{
          responseType: 'arraybuffer',
          headers: {
            "Access-Control-Allow-Origin": "*",
          }
        }).then(res => {
          let str = iconv.decode(Buffer.from(res.data), 'utf-8')
          let html = iconv.encode(str, 'utf8').toString()
          let episode = 0
          const cheerioObj = cheerio.load(html)
          episode = cheerioObj(cheerioObj('.o_r_contact > ul > li > font')[0]).text()
          let episodeNum = episode.match(/\d{1,3}/) ? parseInt(episode.match(/\d{1,3}/)[0]) : 0
          resolve(episodeNum)
        }).catch(error => {
          reject(error)
        })
      }))
    } catch (error){
      console.log(error)
    }
  }
}

